.app {
  display: flex;
  flex-direction: column;
}
.app__container {
  width: 100%;
  display: flex;
  flex-direction: column;
}
@media (min-width: 992px) {
  .app {
    display: flex;
    flex-direction: row;
  }
}
.app__hr {
  margin: 0;
  border: 0;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
}
