.projects {
  background-color: #eeeeee;
}
.projects__container {
  padding: 3rem 0;
}
.projects__header {
  font-size: 3rem;
  font-weight: 700;
  text-transform: uppercase;
  color: #343a40;
  border-bottom: 5px solid #feb97e;
  margin-bottom: 40px;
}
.projects__list {
  display: flex;

  padding-top: 40px;
}
.projects__grid {
  justify-content: space-evenly;
}
.project__icon {
  margin-left: 8px;
}
.pulse {
  animation: pulse-animation 2s infinite;
}

@keyframes pulse-animation {
  0% {
    box-shadow: 0 0 0 0px rgba(62, 129, 253, 0.5);
  }
  100% {
    border-radius: 50%;
    box-shadow: 0 0 0 8px rgba(0, 0, 0, 0);
  }
}
