.about {
  background-color: #204d59;
  /*  #142e35  #d88e4b*/
  color: white;
}
.about__container {
  padding: 3rem;
  padding-bottom: 90px;
}
.about__desciption {
  max-width: 1020px;
  color: rgb(241, 241, 241);
  margin: 0 auto;
}
.about__desciption > p {
  margin-bottom: 16px;
  font-size: 18px;
  line-height: 1.75;
}
.about__contact {
  margin-top: 40px;
  font-size: 1.2em;
  text-align: center;
  color: rgb(241, 241, 241);
}
.about__contact__field > h6 {
  font-size: 1.4rem;
  margin-bottom: 16px;
}
.about__contact__field > h6 > a {
  color: #feb97e;
  text-decoration: none;
}
.about__contact__field > h6 > a:hover {
  color: #a4beba;

  border-bottom: 2px solid #feb97e;
}
.about__contact__field {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-bottom: 30px;
  margin-top: 30px;
  align-items: center;
}
.about__contact__subheader {
  color: #fff;
  margin-bottom: 16px;
  font-weight: 400;
}
.about__contact__email {
  font-size: 1.4rem;
  font-weight: bold;
  color: #feb97e;
}
.about__contact__email:hover {
  color: #a4beba;
  cursor: pointer;
  border-bottom: 4px solid #feb97e;
}
