.home {
  position: relative;
  min-height: 100vh;
  height: 800px;
  /* min-width: 1024px; */
  width: 100%;
  background: #161415 url(./images/mountain-morning.jpg) no-repeat top center;
  background-size: cover !important;
  -webkit-background-size: cover !important;
  text-align: center;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.home__banner {
  display: inline-block;
  vertical-align: middle;
  margin: 0 auto;
  width: 85%;
  padding-bottom: 80px;
  text-align: center;
}
.home__bannerText {
  width: 100%;
  text-align: center;
  text-transform: uppercase;
  padding-bottom: 120px;
  vertical-align: middle;
}

.home__bannerHeader {
  color: rgb(255, 255, 255);
  letter-spacing: -2px;
  text-shadow: rgb(97 131 135) 0px 4px 6px;
  font: 80px / 1.1em opensans-bold, sans-serif;
  margin: 0px auto 18px;
  font-weight: bold;
}
.home__bannerSubtext {
  color: #628d9a;
  margin: 0 auto;
  width: 70%;
}
.home__bannerButton {
  padding-top: 16px;
}
.home__bannerButton > .MuiButton-outlined {
  border-color: #628d9a;
  color: #628d9a;
}
