.work {
  padding: 0 3rem;
  height: 100vh;
}
.work__item {
  margin-top: 4rem;
}

@media (min-width: 768px) {
  .work__item {
    max-width: 75rem;
    display: flex;
  }
  .work__date {
    text-align: right;

    min-width: 18rem;
  }
}
.work__date {
  padding-top: 16px;
  padding-bottom: 16px;
  color: rgb(78, 133, 138);
}
.work__description > h3 {
  font-size: 1.8rem;
}
.work__container {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  max-width: 1000px;
  margin: 0 auto;
}
.work__points > li {
  margin-top: 16px;
}
