.navbar {
  text-align: center;
  position: sticky;
  top: 0;
  left: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -ms-flex-direction: row;
  flex-direction: row;
  width: 100%;
  height: 4rem;
  background-color: #4e858a;
  /* #e59b47 */
  z-index: 10;
  justify-content: center;
}
.navbar__mobile {
  color: white;
  display: flex;
  align-items: center;
  width: 100%;
  justify-content: space-between;
  border-bottom: 1px solid #406b6e;
  padding: 0 16px;
}
.navbar__mobile > h1 {
  font-family: sans-serif;
}
.navbar__mobileCollapse {
  margin-top: 4rem;
  z-index: 100;
  position: fixed;
  background-color: #4e858a;
  width: 100%;
  display: flex;
  flex-direction: column;
}
.collapse {
  display: none;
}
.navbar__mobileCollapse > ul {
  list-style: none;
  padding-left: 0;
  padding-bottom: 8px;
}
.navbar > ul {
  display: none;
}
@media (min-width: 992px) {
  .navbar__mobileCollapse {
    display: none;
  }
  .navbar > ul {
    display: flex;
  }
  .navbar__mobile {
    display: none;
  }
  .navbar {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    width: 16rem;
    height: 100vh;
    z-index: 10;
    justify-content: center;
  }
  .navbar__nav {
    list-style: none;
    padding-left: 0;
    display: flex;
    flex-direction: column;
  }
}
.navbar__profile {
  width: 100px !important;
  height: 100px !important;
  margin-bottom: 20px;
  border: 8px solid #f3c086;
}
.navbar__nav {
  list-style: none;
  padding-left: 0;
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: center;
}
.navbar__item {
  padding: 0.5rem 1rem;
}
.navbar__link {
  color: rgb(231, 230, 230);
  font-size: 1.4em;
  font-weight: bold;
  margin-top: 1em;
  text-decoration: none;
  overflow: hidden;
  transition: 100ms ease-in-out;
  cursor: pointer;
}
.navbar__mobileLink {
  color: rgb(231, 230, 230);
  font-size: 1.4em;
  margin-top: 1em;
  text-decoration: none;
  overflow: hidden;
  transition: 100ms ease-in-out;
  cursor: pointer;
}
.navbar__selected {
  color: #feb97e;
}
.navbar__link:hover {
  padding-bottom: 4px;
  border-bottom: 3px solid #feb97e;
  color: #feb97e;
}
/* .navbar__mobile > .MuiButton-contained {
  color: rgb(255 255 255);
  background-color: #204d59;
} */
.navbar__menuIcon {
  color: white;
}
.navbar__social {
  padding-bottom: 16px;
  color: white;
  cursor: pointer;
  width: 100%;
  justify-content: center;
  align-items: center;
  display: flex;
  flex-direction: row;
  transition: ease-in-out 300ms;

  text-decoration: none;
}
.navbar__socialText {
  padding-left: 8px;
  display: none;
}
.navbar__social:hover {
  color: #feb97e;
}
.navbar__social:hover > .navbar__socialText {
  display: block;
}
.navbar--socials {
  padding-top: 20px;
}
