.contact {
  background: #2b2b2b;
  overflow: hidden;
  padding: 3rem;
}
.contact__container {
  max-width: 650px;
  color: white;
  margin: 0 auto;
  height: 100%;
}
.contact__form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
}
.contact__label {
  font: 15px/24px 'opensans-bold', sans-serif;
  margin: 12px 0;
  color: #ebeeee;
  display: inline-block;
  width: 26%;
  font-weight: bold;
}
.contact__row {
  display: flex;
  flex-direction: column;
  width: 100%;
}
.contact__input {
  padding: 18px 20px;
  color: #eee;
  background: #373233;
  margin-bottom: 42px;
  border: 0;
  outline: none;
  font-size: 15px;
  line-height: 24px;
  width: 100%;
}
.contact__submit {
  height: 56px;
  padding: 0 40px;
  text-transform: uppercase;
  letter-spacing: 3px;
  color: #fff;
  background: #0d0d0d;
  border: none;
  cursor: pointer;
  display: inline-block;
  border-radius: 3px;
  margin: 0 auto;
}
.contact__submit:hover {
  background-color: rgb(68, 68, 68);
}
.contact__field > h6 {
  font-size: 1.4rem;
  margin-bottom: 16px;
}
.contact__field > h6 > a {
  color: #e59b47;
  text-decoration: none;
}
.contact__field > h6 > a:hover {
  color: #4e858a;

  border-bottom: 2px solid #e59b47;
}
.contact__field {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-bottom: 30px;
  align-items: center;
}
.contact__subheader {
  color: #fff;
  margin-bottom: 16px;
  font-weight: 400;
}
.contact__email {
  font-size: 1.4rem;
  font-weight: bold;
  color: #e59b47;
}
.contact__email:hover {
  color: #4e858a;
  cursor: pointer;
  border-bottom: 4px solid #e59b47;
}
